module.exports = "<nv-head page-title=购物车 menu-bar=true></nv-head><div class=page-loading id=freshen>加载中...</div><div class=cart-list v-for=\"list in cartlist\"><div class=cart-list-header><label for={{list.pid}} @click=checkboxs(list)><input type=checkbox v-model=list.isDefault id={{list.pid}}>[单品]</label><div class=pull-right><span class=price-color v-text=\"list.price * list.num | currency '￥'\"></span></div></div><div class=cart-list-body><div class=cell-thumb><img src=" + require("../assets/images/logo.png") + " alt=\"\"></div><div class=cell-bd><h3 v-text=list.title></h3><div><p>颜色：白色 尺码：36</p><p><span class=price-color v-text=\"list.price | currency '￥'\"></span> 运费：<span class=gray>￥9</span></p></div></div></div><div class=cart-list-footer><button class=\"button btn-empty btn-small\" @click=del(list)><i class=iconfont>&#xe607;</i></button><div class=\"pull-right ui-stock\"><button type=button :disabled=\"list.num == 1\" @click=lessNum(list)>-</button> <input name=\"\" @blur=aaa(list) v-model=list.num lazy> <button type=button :disabled=\"list.stock == list.num\" @click=addNum(list)>+</button></div></div></div><div class=cart-list v-if=\"cartlist.length != 0\"><div class=clearfix><div class=pull-left><label for=all><input type=checkbox id=all>删除选中</label></div><div class=\"pull-right price-color\" @click=delAll>清空购物车</div></div></div><div v-else class=alert-status transition=cart><div class=alert-inner><i class=iconfont>&#xe60e;</i><p>您的购物车尚未添加商品</p><button class=\"button btn-important\" v-link=\"{path: '/list'}\">去购物</button></div></div><div class=bot-bar v-if=\"cartlist.length != 0\"><div class=pull-left><span>合计： 12323</span></div><div class=\"pull-right settlement-btn\"><span>结算(2)</span></div></div><nv-alert :content=alert.txt :show=alert.show :class=alert.type :confirm-content=confirm.txt :confirm-show=confirm.show :confirm-func=comfirm></nv-alert>";


